<template>
  <div>

    <van-cell-group v-if="dataList && dataList.length>0">
      <van-cell class="cell_main" v-for="(item, index) in dataList" :key="index"
                :style="{paddingLeft: '0', paddingRight:'0'}" is-link @click="typeSelect(item)">
        <template #icon>
          <!--          <van-icon class="line_icon" class-prefix="iconfont icon-zuoye"></van-icon>-->
          {{index + 1}}、
        </template>
        <template #title>
          <div class="flex-between line_title">
            <span>{{item.name}}</span>
            <span>
              题数：
              <span style="color: #00cbab;">{{item.total}}</span>
            </span>
          </div>
        </template>
      </van-cell>
    </van-cell-group>
    <r-lack-data v-else></r-lack-data>

  </div>
</template>

<script>
import { screenQuestionTypeName } from "@/utils/enum";

export default {
  name: "ScreenQuestion",
  data () {
    return {
      courseID: this.$store.getters.courseID,
      courseInfo: this.$store.getters.course,

      dataList: [],
      dataLoading: false
    }
  },
  created () {
    this.GetScreenQuestionStats();
  },
  methods: {
    // 获取截屏提问类型统计
    GetScreenQuestionStats () {
      this.dataLoading = true;
      this.dataList = [];
      this.$mainHttp.screenQuestion.dataStatistics({
        ciID: this.courseID
      }).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.dataList.push({
              type: item["Type"],
              name: screenQuestionTypeName(item["Type"]),
              total: item["Total"] || 0
            });
          });
        }
        this.dataLoading = false;
      }).catch(() => {
        this.dataLoading = false;
      });
    },
    typeSelect (item) {
      this.$router.push("/course-record/screen-question/list/" + item.type);
    }
  }
}
</script>

