<template>
  <div class="record-item ">
    <div class="item-title flex-between">
      <span>小组排名</span>

      <van-dropdown-menu v-if="groupTimeOptions && groupTimeOptions.length > 0">
        <van-dropdown-item v-model="groupTime" :options="groupTimeOptions" @change="changeGroupTime"/>
      </van-dropdown-menu>
    </div>

    <van-cell-group v-if="groupList && groupList.length>0">
      <van-cell class="cell_main" v-for="(item, index) in groupList" :key="index"
                :style="{paddingLeft: '0', paddingRight:'0'}" @click="groupSelect(item)">
        <template #icon>
          <van-icon class="line_icon" class-prefix="iconfont icon-wangguan"></van-icon>
        </template>
        <template #title>
          <div class="flex-between line_title">
            <span>{{item.name}}</span>
            <span>排名：
                <van-tag type="primary">{{item.rank}}</van-tag>
              </span>
          </div>
        </template>
        <template #label class="flex-between">
          <div class="flex-between line_title">
            <span> 总分：{{item.scoreTotal || 0}}</span>
            <span> 总赞：{{item.goodTotal || 0}}</span>
            <span></span>
            <span>成员：查看</span>
          </div>
        </template>
      </van-cell>
    </van-cell-group>
    <r-lack-data v-else></r-lack-data>

    <van-popup v-model="dialogVisible" position="bottom" closeable>
      <div class="sub_nav">
        <span>小组成员({{groupSelected ? groupSelected.name : ''}})</span>
      </div>
      <van-cell-group v-if="groupMember && groupMember.length>0">
        <van-cell class="cell_main" v-for="(item, index) in groupMember" :key="index">
          <template #icon>
            <img :src="defaultHead" alt=""/>
          </template>
          <template #title>
            <div class="flex-between line_title">
              <span>{{item.name}}</span>
              <span>排名：
                <van-tag type="primary">{{item.rank}}</van-tag>
              </span>
            </div>
          </template>
          <template #label class="flex-between">
            <div class="flex-between line_title">
              <van-tag plain  :type="item.isLeader ? 'warning' : 'success'">
                {{item.isLeader ? '组长' : '成员'}}
              </van-tag>
              <span></span>
              <span> 总分：{{item.scoreTotal || 0}}</span>
              <span> 总赞：{{item.goodTotal || 0}}</span>
            </div>
          </template>
        </van-cell>
      </van-cell-group>
      <r-lack-data v-else></r-lack-data>
    </van-popup>
  </div>
</template>

<script>
import defaultImg from "../../../../../assets/boy_img.png";

export default {
  name: "GroupRank",
  data () {
    return {
      courseID: this.$store.getters.courseID,
      courseInfo: this.$store.getters.course,
      defaultHead: defaultImg,

      groupTime: "",
      groupTimeOptions: [],

      groupList: [],
      isLoading: false,

      groupSelected: null,
      groupMember: [],
      dialogVisible: false
    };
  },
  created () {
    this.getGroupTimes();
  },
  methods: {
    getGroupTimes () {
      this.groupTimeOptions = [];
      this.$mainHttp.group.dataList({
        ciID: this.courseID
      }).then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.groupTimeOptions.push({
                text: `第${item["GroupingNumber"]}次分组`,
                value: item["GroupingNumber"], // item["GroupingID"],
                number: item["GroupingNumber"],
                courseID: item["CIID"]
              });
            });
            this.groupTime = res.Datas[0]["GroupingNumber"];
            this.changeGroupTime(this.groupTime);
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    changeGroupTime (value) {
      this.groupList = [];
      this.getGroupRank(value);
    },
    getGroupRank (value) {
      this.isLoading = true;
      this.$mainHttp.group.dataRanking({
        ciID: this.courseID,
        number: value
      }).then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.sort((a, b) => {
              return a["Ranking"] > b["Ranking"] ? 1 : -1;
            });
            res.Datas.forEach(item => {
              this.groupList.push({
                id: item["GroupID"],
                name: item["GroupName"],
                rank: item["Ranking"],
                goodTotal: item["TotalGood"],
                scoreTotal: item["TotalScore"]
              });
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    },
    groupSelect (item) {
      this.groupSelected = item;
      this.groupMember = [];
      this.getGroupMember();
      this.dialogVisible = true;
    },
    getGroupMember () {
      this.isLoading = true;
      this.$mainHttp.group.memberRanking({
        groupID: this.groupSelected.id
      }).then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.sort((a, b) => {
              return a["Ranking"] > b["Ranking"] ? 1 : -1;
            });
            res.Datas.forEach(item => {
              this.groupMember.push({
                name: item["StudentName"],
                isLeader: item["IsGroupLeader"],
                rank: item["Ranking"],
                goodTotal: item["TotalGood"],
                scoreTotal: item["TotalScore"]
              });
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .van-dropdown-menu {
  height: unset;
}
</style>
