<template>
  <div class="record-item ">
    <div class="item-title flex-between">
      <span>随机挑人</span>

      <van-dropdown-menu v-if="randomTimeOptions && randomTimeOptions.length > 0">
        <van-dropdown-item v-model="randomValue" :options="randomTimeOptions"/>
      </van-dropdown-menu>
    </div>

    <div class="flex-wrap-between" v-if="personList && personList.length > 0">
      <div v-for="(sub, k) in personList" :key="k" class="student_card van-hairline--surround">
        <img :src="defaultHead" class="col_4" alt=""/>
        <div>{{sub.name|| '姓名' }}</div>
      </div>
      <div class="student_card" style="width: 19vw;" v-for="d in Math.abs(4 - personList.length % 4) % 4"
           :key="-d">
      </div>
    </div>
    <r-lack-data v-else></r-lack-data>

  </div>
</template>

<script>
import defaultImg from "../../../../../assets/boy_img.png";

export default {
  name: "RandomPerson",
  data () {
    return {
      courseID: this.$store.getters.courseID,
      courseInfo: this.$store.getters.course,
      defaultHead: defaultImg,

      randomValue: "",
      randomTimeOptions: [],

      groupList: [],
      isLoading: false
    };
  },
  computed: {
    personList () {
      const index = this.randomTimeOptions.findIndex(r => r.value === this.randomValue);
      return index > -1 ? this.randomTimeOptions[index].personList : [];
    }
  },
  created () {
    this.getRandomList();
  },
  methods: {
    getRandomList () {
      this.randomTimeOptions = [];
      this.$mainHttp.randomPick.dataList({
        ciID: this.courseID
      }).then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              let personList = [];
              if (item.Value && item.Value.length > 0) {
                item.Value.forEach(sub => {
                  personList.push({
                    id: sub["StudentID"],
                    name: sub["StudentName"]
                  });
                });
              }
              this.randomTimeOptions.push({
                text: `第${item.Key["RPSerial"]}次挑人`,
                value: item.Key["RPID"], // item["GroupingID"],
                number: item.Key["RPSerial"],
                courseID: item.Key["CIID"],
                personList
              });
            });
            this.randomValue = this.randomTimeOptions[0].value;
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .van-dropdown-menu {
  height: unset;
}
</style>
